@import url(https://fonts.googleapis.com/css2?family=Hind:wght@600;700&family=Work+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  --color-black5: #f5f5f5;
  --color-black10: #eeeeee;
  --color-black20: #dddddd;
  --color-black60: #6f6f6f;
  --color-black80: #444444;

  --font-body: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  --font-heading: 'Hind', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

h1 {
  font-family: var(--font-heading);
  font-weight: 700;
  font-size: 24px;
  margin: 0;
}

.Content {
  flex: 1 1;
  padding: 40px;
  max-width: 300px;
}
.MovieCard {
  background: #fff;
  box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.05), -2px -4px 12px  rgba(90, 90, 90, 0.1);
  border-radius: 12px;
  text-align: left;
}

.MovieCard-text {
  padding: 10px 16px;
  padding-top: 6px;
}

.MovieCard h3 {
  color: black;
  margin: 0;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 16px;
}

.MovieCard-year {
  color: var(--color-black80);
  font-size: 14px;
}

.MovieCard-poster {
  height: 237px;
  width: 160px;
  object-fit: cover;
  border-radius: 12px 12px 0 0;
}
.SearchPanel {
  flex: 1 1;
  padding: 0 40px;
  background: var(--color-black5);
}

.SearchPanel h1 {
  font-family: var(--font-heading);
  font-weight: 700;
  font-size: 22px;
  margin: 0;
}

.SearchPanel-heading-bar {
  display: flex;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 60px;
}

.SearchPanel-heading-bar .search {
  flex: 1 1;
  border: none;
  box-sizing: border-box;
  font-family: var(--font-body);
  font-size: 16px;
  outline: none;
  padding: 8px 30px;
  background: white;
  border-radius: 24px;
  height: 44px;
  margin-left: 30px;
}

.SearchPanel-content {
  text-align: center;
  color: var(--color-black60);
  padding-bottom: 40px;
  font-size: 18px;
}

.SearchPanel-movie-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 160px);
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  grid-row-gap: 30px;
  row-gap: 30px;
  justify-content: center;
}
